<template>
    <div class="banner">
        <div class="slider-wrap">
            <ul class="slider-items" :style="sliderActive" @touchstart="stopSlider($event)" @touchmove="moveSlider($event)" @touchend="continSilder($event)">
                <li class="slider-item" v-for="item in sliderImg"><img :src="item.url"></li>
            </ul>
        </div>
        <ul class="banner-page">
            <li class="slider-btn" v-for="(item,index) in bannerlist" :class="{'active':canActive(index)}"></li>
        </ul>
    </div>
</template>
<script>
export default {
    name : 'banner',
    props : {
        bannerlist : {
            require : true
        }
    },
    data () {
        return {
            sliderActive : {
                transform: 'translate3d(-100vw,0,0)',
                transition:'transform 2s'
            },
            interTimer : '',
            startTouch : '',
            moveTouch : '',
            canslider : true,
            nowSlider : 0
        }
    },
    computed : {
        sliderBtn : function(){
            return this.bannerlist
        },
        sliderImg : function(){
            const [...saveImg] = this.bannerlist,
                  [imgfirst,otherImg,imglast] = this.bannerlist;
             saveImg.unshift(imglast)     
             saveImg.push(imgfirst)
            return saveImg
        },
    },
    mounted () {
        this.interTimer = setInterval(()=>{
            this.sliderStart()
        },3000)
    },
    methods : {
        stopSlider (e) {
            if (e.target != e.currentTarget) {  //事件委托节省下事件绑定，排除当前绑定的dom
                clearInterval(this.interTimer)
                delete this.sliderActive['transition'] //关闭css3过渡效果
                this.startTouch = e.targetTouches[0].screenX
            }
        },
        moveSlider (e) {
            if (e.target != e.currentTarget) {
                let slideDir = this.moveTouch - this.startTouch,
                    targetWidth = parseInt(window.getComputedStyle(e.target).width),
                    tranDir;
                this.moveTouch = e.targetTouches[0].screenX
                if (slideDir < -targetWidth) {
                    // if (slideDir<0) {
                    //     slideDir
                    // }
                    // slideDir = -targetWidth
                }
                tranDir = -targetWidth * (this.nowSlider+1) + slideDir
                this.sliderActive.transform = `translate3d(${tranDir}px,0,0)`//触摸时图片随手指移动，距离须减去当前图片宽度乘以当前滚动索引
            }
        },
        continSilder (e) { //结束触摸
            if (e.target != e.currentTarget) {
                const slideDir = this.moveTouch - this.startTouch
                if (slideDir < 0) {
                    this.nowSlider ++
                } else if (slideDir > 0) {
                    this.nowSlider -- 
                }
                this.nowSlider --
                if (this.nowSlider < -1) {
                    this.nowSlider = -1
                }
                this.sliderStart() //立即设置位置
                this.interTimer = setInterval(()=>{
                    this.sliderStart()
                },3000)
            }
        },
        canActive(index){
            return index === this.nowSlider
        },
        sliderStart () {
           this.nowSlider ++ 
           this.nowSlider %= this.sliderImg.length
           this.sliderActive = Object.assign({},{
               transition:'transform 0.5s',
               transform: `translate3d(${-100*(this.nowSlider+1)}vw,0,0)`
           })  
        } 
    }
}
</script>
<style scoped lang="scss">
@import '~assets/css/theme.scss';
.banner{
   position:relative;
}
.banner-page{
   position:absolute;
   bottom:1rem;
   justify-content:center;
   display: flex;
   align-items:center;
   width:100%;
}
.slider-btn{
    width:0.8rem;
    height:0.8rem;
    background-color:#aaa;
    border-radius: 50%;
    margin-right:0.5rem;
}
.slider-btn.active{
    background:$themeColor;
}
.slider-wrap{
    overflow:hidden;
}
.slider-items{
    font-size:0;
    width:5*100vw;
    overflow: hidden;
}
.slider-item {
    width: 100vw;
    float: left;
}
.slider-item>img{
    width: 100%;
}
</style>


